import { ToastAlert } from "../Functions"
import React,{useEffect,useState,Suspense} from 'react'

export function ItemOnCredit(props){
const [status,setStatus]=useState('')
    return(<div style={{padding:"5px"}}>

<div style={{boxShadow:"0px 0px 4px grey",padding:"5px"}}>
<div style={{fontSize:"20px",fontWeight:"600",textAlign:"center",borderBottom:"0.5px solid grey"}}>{props.label}</div>
<div style={{paddingBottom:"3px",paddingTop:"4px",textAlign:"center"}}>{props.description}</div>

<div style={{fontSize:"10px",textAlign:"center"}}>{props.location}</div>
<div style={{fontSize:"20px",textAlign:"center",padding:"8px"}}> <span style={{color:"green",padding:"5px",fontWeight:"600"}}>{props.bill}/=</span></div>
<div style={{textAlign:"center",fontSize:"15px",color:"black"}}>{status}</div>
<div class="button1"
onClick={()=>{
    setStatus('Please wait........')
    setTimeout(()=>{
        if(window.confirm(`Complete the transaction to debit user with ${props.bill}/=`)===true){
            
            setStatus('Successful')
        }else{
            setStatus('Cancelled')
        }
    },2000)
    
}}
>Complete transaction</div>
    
</div>
    </div>)
}

export default ItemOnCredit